
import React from 'react';


const ProgressBar = (props) => {

    return (
        <div className={props.className}>
            <h4>{props.title}</h4>
            <div className="years">
                <span>Expérience en années</span>
                <span>1 an</span>
                <span>2 ans</span>

            </div>

            <div>
                {
                    props.data.map((item) =>{

                        let xpYears = 2;
                        let progressBar = item.Use_Percentage / xpYears *100 + '%';
                        console.log(progressBar)
                        return(
                            <div key={item.Id} className="languageList">
                                <ul>
                                    <li>{item.TechnoName}</li>
                                </ul>
                    
                                <div 
                                    className="progressBar" 
                                    style={{width:progressBar}}>
                                </div>

                            </div>
                            
                        )
                      
                    
                    })
                }
            </div>

        </div>

      
    );
};

export default ProgressBar;