import React from "react";
import Lifevent from "../Lifevent";
import TitleSection from "../TitleSection";

import certificationsData from "./Data/DataCertifications";

const CertificationsMenu = () => {
    return (
        <div className="LG-lifevent-title">

                <TitleSection catName="Certifications" fontAwesome="fas fa-flag"></TitleSection>
                  
            <ul>
                {certificationsData.map(({url, company, begindate, enddate, role, description}) =>(
                    <Lifevent url={url} company={company} begindate={begindate} endate={enddate} role={role} description={description}/>
                ))}     
            </ul> 
        </div>
    );
};

export default CertificationsMenu;