import React from 'react';
import ContactFooter from '../components/ContactFooter';
import CertificationsMenu from '../components/cv/CertificationsMenu';
import EducationMenu from '../components/cv/EducationMenu';
import ExperienceMenu from '../components/cv/ExperienceMenu';
import KnowledgesMenu from '../components/cv/KnowledgesMenu';

import NavMenu from '../components/NavMenu';
import Navigations from '../components/Navigations';


import { makeStyles } from '@material-ui/core/styles';

import Grid from '@material-ui/core/Grid';
import Identity from '../components/Identity';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },

    grid :{
    width : '100%',
    margin: '0px'
    },

    paper : {
        padding: theme.spacing(2),
    }
  }));



const Home = () => {
    const classes = useStyles();

    return (
        
        
        
        <Grid container spacing={2} className={classes.grid} direction="column">
            <Navigations/>
                        <Identity/>
                        <NavMenu></NavMenu>
                        <ExperienceMenu/>
                        <KnowledgesMenu/>
                        <CertificationsMenu/>
                        <EducationMenu/>
                        <footer>
                            <ContactFooter/>
                        </footer>
     
        </Grid>
     
    );
};

export default Home;