import React, { Component, useState, useEffect } from 'react';
import ProgressBar from './ProgressBar';
import  { csv } from 'd3';

const CSVurl = "https://gist.githubusercontent.com/listapuce/5b644d0400c677c513af703e40fb18b7/raw/08d9e126f305e7e3d4a38ec841f685a6502f5433/gistfile1.csv"


const Languages = () => {
    const [data, setData] = useState([]);
    useEffect(()=> {
        csv(CSVurl).then(data=>{
        setData(data)
        });
    
    }, []);
    
    const LanguageFilter = data.filter(x => x.TechnoType === "Language" && x.Open_Skill === "Yes")
    const FrameworkFilter = data.filter(y => y.TechnoType === "Framework" && y.Open_Skill === "Yes")
    
    return (
        <div className="ProgressBarDisplay">
            <ProgressBar
                data = {LanguageFilter}
                className = "LanguageDisplay"
                title = "Languages" 
            />

            <ProgressBar
                data = {FrameworkFilter}
                className = "FrameworkDisplay"
                title = "Framework" 
            />
            
        </div>
    );
}

export default Languages;
