
import { NavLink } from 'react-router-dom';
import React from 'react';



const NavMenu = () => {
    return (

        <div id="NavigationMenu">
            <div className= "id-NavMenu">

                <NavLink exact to="/" activeClassName="navActive">
                    <a href="#/" className="nav-Item"><span>ALL</span></a>
                    
                </NavLink>

                <NavLink exact to="/experience" activeClassName="navActive">
                <a href="#/experience" className="nav-Item"><span>Experience</span></a>
                </NavLink>


                <NavLink exact to="/knowledge" activeClassName="navActive">
                <a href="#/knowledge" className="nav-Item"><span>Skills</span></a>
                </NavLink>

                <NavLink exact to="/education" activeClassName="navActive">
                <a href="#/education" className="nav-Item"><span>Education</span></a>
                </NavLink>

                <NavLink exact to="/certifications" activeClassName="navActive">
                <a href="#/certifications" className="nav-Item"><span>Certifications</span></a>
                </NavLink>




         
        
            </div>
        </div>
    );
};

export default NavMenu;