import React from 'react';



function getCurrentDate(){

    let newDate = new Date()
    let year = newDate.getFullYear();
    
    return `${year}`
    }



const Contact = () => {
    return (
        <div className="contact-footer">

            <div className="social-networks">

            
            <a href="http://twitter.com/GravelinLeo" target="_blank" rel="noopener noreferrer"> <i class="fab fa-twitter"></i></a>
          

            </div>


            <div id="copyright">
            <p>Copyright <span>{getCurrentDate()}</span> &copy; Léo Gravelin</p>
            </div>
                        
        </div>
    );
};

export default Contact;




