import React from 'react';
const Lifevent = (props) => {

    return (

      
            <li className="life_event">
                <span className="life-period">
                    <span><a href={props.url} target="_blank" rel="noopener noreferrer">{props.company}</a></span><span> | </span><span>{props.begindate} - {props.endate}</span>
                    <span> - {props.role}</span>
                </span>
                <br></br>
                <p>{props.description}</p>

            </li>
      
    );
};

export default Lifevent;