import React from 'react';
const TitleSection = (props) => {

    return (
           <div class="titleSection">
        
                <div class="titleContainer titleFlex">
                    <div class="titleFlex titleFas">
                        <i className={props.fontAwesome}/>
                    </div>
                    <div class="titleFlexPlus titleFlex">
                        <h3>{props.catName}</h3>
                    </div>
                    
                    <hr class="titleHR"/>
                </div>
            </div>


  
            
     
      
    );
};

export default TitleSection;