import React from 'react';
import Lifevent from '../Lifevent';
import TitleSection from '../TitleSection';

import educationData from './Data/DataEducation';

const EducationMenu = () => {
    return (
        <div className="LG-lifevent-title">
            

            <TitleSection catName="Education" fontAwesome="fas fa-graduation-cap"></TitleSection>

            <ul>
                {educationData.map(({url, company, begindate, enddate, description})=> (                <Lifevent url={url} company={company}begindate={begindate} endate={enddate} description={description} />
                ))}

            </ul>
        </div>
    );
};

export default EducationMenu;