import React from 'react';

import Navigations from '../components/Navigations';
import Languages from '../components/knowledges/Languages.js';
import OthersSkills from '../components/knowledges/OthersSkills.js';

import Identity from '../components/Identity';
import NavMenu from '../components/NavMenu';

import TitleSection from '../components/TitleSection';

const Knowledges = () => {
    return (
        <div>

            <Identity/>
            <Navigations/>
            <NavMenu></NavMenu>

            
            <div className="LG-lifevent-title">


                <TitleSection catName="Skills" fontAwesome="fas fa-users-cog"></TitleSection>   

            </div>
            <div className="knowledges"> 
                <Languages/>
                <OthersSkills/>

                    1rst version : Level bars skills languages
                    2nd version : All about skills languages in 3DJS values. 
            </div>
            
                
           
        </div>
    );
};

export default Knowledges;