import { orange } from '@material-ui/core/colors';
import React from 'react';
import Typewriter from 'typewriter-effect'


const Availablepoint = (props) => {
    
    let Availability = ["Full-Time available", "Not Available, fully engaged", "Slots available"]
    // la couleur et le texte change en fonction de la disponibilité 
    //(voir API planning et lago dédié)
    
    return (

        <Typewriter 
        options={{
            autoStart: true,
            cursor: "",
            delay:"20"
        }}
        
        className={props.className}
        id="typewriterID" 
     
        
        onInit={(typewriter) => {
            typewriter.typeString(Availability[0]).stop().changeDelay(2).start();
        }}/>

    

    );
};

export default Availablepoint;