import React from 'react';


import Languages from '../knowledges/Languages';
import OthersSkills from '../knowledges/OthersSkills.js';


import TitleSection from '../TitleSection';

const Knowledges = () => {
    return (
        <div>

            <div className="LG-lifevent-title">


                <TitleSection catName="Skills" fontAwesome="fas fa-users-cog"></TitleSection>   

            </div>
            <div className="knowledges"> 
                <Languages/>
                <OthersSkills/>

                    1rst version : Level bars skills languages
                    2nd version : All about skills languages in 3DJS values. 
            </div>
            
                
           
        </div>
    );
};

export default Knowledges;