import React from 'react';
import Lifevent from '../Lifevent';
import TitleSection from '../TitleSection';

//CONST
import  experienceData from './Data/DataExperience'

const ExperienceMenu = () => {
    return (
        <div className="LG-lifevent-title">
            <TitleSection catName="Experience & Entrepreneurship" fontAwesome="fas fa-users-cog"></TitleSection>      
            <ul>
                {experienceData.map(({ company, begindate, enddate, details, role}) => (
                <Lifevent company={company} begindate={begindate} endate={enddate} role={role} description={details}/>
                ))}
            </ul>
        </div>
    );
};
export default ExperienceMenu;

