
import { NavLink } from 'react-router-dom';
import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuItem from '@material-ui/core/MenuItem';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuList from '@material-ui/core/MenuList';
import DehazeIcon from '@material-ui/icons/Dehaze';


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
  }));



const Navigations = () => {


    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
  
    const handleToggle = () => {
      setOpen((prevOpen) => !prevOpen);
    };
  
    const handleClose = (event) => {
      if (anchorRef.current && anchorRef.current.contains(event.target)) {
        return;
      }
  
      setOpen(false);
    };
  
    function handleListKeyDown(event) {
      if (event.key === "Tab") {
        event.preventDefault();
        setOpen(false);
      }
    }
  
    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
      if (prevOpen.current === true && open === false) {
        anchorRef.current.focus();
      }
  
      prevOpen.current = open;
    }, [open]);
  
    return (

   
    <div className={classes.root}>

      <AppBar position="fixed" color="secondary" id='AppBar'>
        <Toolbar>
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            
            <Button
            ref={anchorRef}
            aria-controls={open ? "menu-list-grow" : undefined}
            aria-haspopup="true"
            onClick={handleToggle}
            >
          <DehazeIcon id="DehazeIcon"/>
         
            </Button>
            <Popper
            open={open}
            anchorEl={anchorRef.current}
            role={undefined}
            transition
            disablePortal
            >
            {({ TransitionProps, placement }) => (
                <Grow
                {...TransitionProps}
                style={{
                    transformOrigin:
                    placement === "bottom" ? "center top" : "center bottom"
                }}
                >
                <Paper>
                    <ClickAwayListener onClickAway={handleClose}>
                    <MenuList
                        autoFocusItem={open}
                        id="menu-list-grow"
                        onKeyDown={handleListKeyDown}
                    >
                        <NavLink exact to="/" activeClassName="navActive">
                            <MenuItem onClick={handleClose}>
                                    <i className="fas fa-home"/>
                                    <span>Home</span>
                            </MenuItem>
                        </NavLink>
                        <MenuItem onClick={handleClose}>
                            <NavLink exact to="/experience" activeClassName="navActive">
                                <i className="fas fa-users-cog"/>
                                <span>Experience</span>
                            </NavLink>
                        </MenuItem>
                        <MenuItem onClick={handleClose}>
                            <NavLink exact to="/education" activeClassName="navActive">
                                <i className="fas fa-graduation-cap"/>
                                <span>Education</span>
                            </NavLink>
                        </MenuItem>
                        <MenuItem onClick={handleClose}>
                            <NavLink exact to="/certifications" activeClassName="navActive">
                                <i className="fas fa-flag"/>
                                <span>Certifications</span>
                            </NavLink>
                        </MenuItem>
                        <MenuItem onClick={handleClose}>
                            <NavLink exact to="/knowledge" activeClassName="navActive">
                                <i className="fas fa-cogs"/>
                                <span>Knowledges</span>
                             </NavLink>
                      
                        </MenuItem>
                        <MenuItem onClick={handleClose}>      
                            <NavLink exact to="/publications" activeClassName="navActive">
                                <i className="fas fa-book-reader"/>
                                <span>Publications</span>
                            </NavLink>
                        </MenuItem>
                        <MenuItem onClick={handleClose}>
                            <NavLink exact to="/skills" activeClassName="navActive">
                                <i className="fas fa-microscope"/>
                                <span>Viz Skills</span>
                             </NavLink>

                             <span><br></br></span>
                            
                             <NavLink exact to="/domains" activeClassName="navActive">
                                <i className="fas fa-globe"/>
                                <span>Viz Domain</span>
                             </NavLink>
                        </MenuItem>
                    </MenuList>
                    </ClickAwayListener>
                </Paper>
                </Grow>
            )}
            </Popper>
          </IconButton>
          <Typography variant="h6" className={classes.title}>
          {/* A title if needed */}
          </Typography>
          <Button color="inherit">
                <NavLink exact to="/portfolio" activeClassName="navActive">
                    <i className="fas fa-eye"/>
                    <span>Portfolio</span>
                </NavLink>
          
          </Button>
          
          <Button color="inherit">
          <NavLink exact to="/contact" activeClassName="navActive">
            <i className="fas fa-smile-wink"/>
            <span>Contact</span>
          </NavLink>

          
          </Button>
        </Toolbar>
      </AppBar>
    </div>
    


    ); 
};

export default Navigations;